import React, { Component } from 'react';
import axios from 'axios'
import swal from 'sweetalert';
import PageLoader from '../../Common/PageLoader'
import ContentWrapper from '../Layout/ContentWrapper';
import { Progress, Button, Row, CardBody, CardHeader, CardFooter, Card, Form, FormGroup, Input } from 'reactstrap';
import Datetime from 'react-datetime';
import 'react-datetime/css/react-datetime.css';
import filterFactory,{textFilter, selectFilter, dateFilter, numberFilter} from 'react-bootstrap-table2-filter';
import paginationFactory, { PaginationProvider, PaginationListStandalone } from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search, CSVExport } from 'react-bootstrap-table2-toolkit';
import HeadingMenu from '../HeadingMenu';
import {typeActivite as type, typeActivite} from '../TypeActivite'
import {url_notifications} from '../../../urls'

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

/**  Custom Formatter */
// format de l'adresse de la machine
function LocaliteFormatter(cell, row) {
    const {lieu, pays, quartier, ville} = new Object(row.localite)
    return (
        <address>
            <span> {quartier} </span>
            <br/>
            <small className="text-italic"> {lieu} </small>
        </address>
    )
}

// format de la date
function DateFormatter(cell, row) {
    const date = new Date(row.createdAt)
    const created = date.toUTCString()
    
    return(
        <span> {created} </span> 
    )
}

// format du status de la machine
function StatusFormatter(cell, row) {
    if (row.status) {
      return (<div className="circle circle-lg bg-success" data-title="normal"/>);
    }

    return (<div className="circle circle-lg bg-danger" data-title="normal"/>);
}

function DescriptionFormatter(cell, row) {
    return ("Un attimo...")
}

// action déclenchée lorsqu'on selectionne une ligne
const selectRow = {
    mode: 'radio',
    clickToSelect: true
};

// header style for datatable
const headingStyle = {
    backgroundColor : '#E5F1FB',
    color: ''
}

class Activites extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            notifs: [],
            path: [
                { 
                    label: 'Home',
                    value: '/dashboard_machine',
                },
                { 
                    label: 'Machine',
                    value: '/list_machine',
                },
                { 
                    label: 'Liste',
                    value: '',
                }
            ],
            isLoading: true,
        },
        this._columns = [
            {
                dataField: 'key',
                text: 'ID',
                hidden: true
            },            
            {
                dataField: 'type',
                text: 'Type',
                headerStyle: headingStyle,
                sort: true
            },
            {
                dataField: 'nom',
                text: 'Nom',
                headerStyle: headingStyle,
                sort: true
            },
            {
                dataField: 'localite',
                text: 'Emplacement',
                headerStyle: headingStyle,           
                formatter: LocaliteFormatter,
                sort: true
            },
            {
                dataField: 'description',
                text: 'Description',
                headerStyle: headingStyle,            
                formatter: DescriptionFormatter,
                sort: true
            },
            {
                dataField: 'date',
                text: 'Date',
                formatter: DateFormatter,
                sort: true
            },
        ];
    }

    componentDidMount() {
        axios.get(`${url_notifications}machines/`)
        .then((res) => {
            const notifs = res.data.data
            this.setState({
                notifs,
                isLoading: false,
            })
        })
        .catch((error) => {
            console.log(error.message)
            swal('Erreur','Désolé une erreur est survenue', 'warning')
        })
    }

    checkDescription(notification) {
        const type = notification.type
        switch (type) {
            case typeActivite[0].value: { // ajout
                return (
                    <NouvelleMachine value = {notification}/>
                )
            }
            case typeActivite[1].value: { // update 
                return(
                <UpdateMachine value = {notification}/>
                )
            }
            case typeActivite[2].value: { // suppression
                return(
                <DeleteMachine value = {notification}/>
                )
            }
            case typeActivite[3].value: { // depot
                return ( 
                    <Depot value = {notification}/>
                )
            }
            case typeActivite[4].value: { // collecte
                return(
                <Collecte value = {notification}/>
                )
            }
            default: {
                return(
                    <DefaultActivite value = {notification}/>
                )
            }
        }
    }

    render() {
        const notifs = this.state.notifs
        const isLoading = this.state.isLoading
        
        const options = {
            custom: true,
            paginationSize: 4,
            pageStartIndex: 1,
            sizePerPage: 4,
            firstPageText: 'First',
            prePageText: 'Back',
            nextPageText: 'Next',
            lastPageText: 'Last',
            nextPageTitle: 'First page',
            prePageTitle: 'Pre page',
            firstPageTitle: 'Next page',
            lastPageTitle: 'Last page',
            showTotal: true,
            sizePerPageList: [
            {
                text: '2', value: 2
            }, 
            {
                text: '5', value: 5
            }, 
            {
                text: 'All',
                value: notifs.length
            }],
            totalSize: notifs.length
        };

        const contentTable = ({ paginationProps, paginationTableProps }) => (
            <div>
              <ToolkitProvider
                keyField="key"
                columns={ this._columns }
                data={ notifs }
                search           
                >
                {
                  toolkitprops => (
                    <div>

                        {/*Fitre et barre de recherche */}
                        <Row>
                            {/*filtre */}
                            <div className="col-xl-7">
                                <Form 
                                name="formFilter" 
                                >
                                    <div className="row">
                                        <div className="ml-3 d-flex">
                                            <em className="m-0 fas fa-filter fa-2x text-success align-self-center"/>
                                            {/*<em> Filtrer vos données ici </em>*/}
                                        </div>
                                        {/*date */}
                                        <FormGroup className="col-xl-3">
                                            <div className="pl-2 mr-2">
                                                <label className="text-bold">Date</label>
                                                <Datetime 
                                                inputProps={{className: 'form-control'}}
                                                defaultValue={day}
                                                value={day}
                                                onChange={this.onChangeDay}
                                                />
                                            </div>
                                        </FormGroup>
                                        {/*month */}
                                        <FormGroup className="col-xl-3">
                                            <div className="pl-2 mr-2">
                                                <label className="text-bold"> Mois </label>
                                                <Datetime 
                                                inputProps={{className: 'form-control'}}
                                                timeFormat={false}
                                                defaultValue={month}
                                                value={month}
                                                dateFormat="YYYY-MM" 
                                                onChange={this.onChangeMonth}
                                                />
                                            </div>
                                        </FormGroup>
                                        {/*status */}
                                        <FormGroup className="col-xl-3">
                                            <div className="pl-2 mr-2">
                                                <label className="text-bold"> Status </label>
                                                <Input 
                                                type="select"
                                                name="status" 
                                                placeholder="etat de la machine" 
                                                onChange={this.onChangeStatus}
                                                value={labelStatus}
                                                >
                                                    {selectStatus.map((status) =>
                                                        <option> {status.label} </option>
                                                    )}
                                                </Input>
                                            </div>
                                        </FormGroup>
                                        {isFilter? (
                                        <div className="row pl-4 d-flex align-items-center">
                                            <span className="fas fa-minus-square text-inverse fa-2x mr-2" onClick={this.clearFilter}/>
                                            <p> Effacer </p>
                                        </div>
                                        ):(
                                        <div className="row pl-4 d-flex align-items-center">
                                            <span className="fas fa-check-square text-success fa-2x mr-2" onClick={this.allowFilter}/>
                                            <p className="align-self-center"> Appliquer </p>
                                        </div>
                                    
                                        )}
                                    </div>
                                </Form>
                            </div>                                        
                            {/*search bar */}
                            <div className="col-xl-4 align-self-center ml-auto">
                                <div className="media">
                                    <div className="media-body">
                                        <SearchBar 
                                            { ...toolkitprops.searchProps }
                                            className="custome-search-field"
                                            style={ { color: 'dark' } }
                                            placeholder="Rechercher un nom, une zone, un niveau de remplissage"                    
                                        />
                                    </div>
                                    <div className="align-self-start mr-2">
                                        <em className="fas fa-search fa-2x "></em>
                                    </div>
                                </div>
                            </div>
                        </Row>
                        {/*table */}
                        <Card className="m-20">
                            <CardBody className="ml-5 mr-5">
                                <BootstrapTable
                                bootstrap4
                                hover
                                filter={filterFactory()}
                                selectRow={selectRow}
                                { ...toolkitprops.baseProps }
                                { ...paginationTableProps }
                                />
                            </CardBody>
                            <CardFooter>
                                
                            </CardFooter>
                        </Card>
                        {/*Print or export */}
                        <div className="col">
                            <div className="media m-10">
                                {/*export icon */}
                                <ExportCSVButton { ...toolkitprops.csvProps } className="d-flex p-0 mr-5" >
                                    <Button color="secondary">
                                        <em className="fas fa-share text-info"></em>
                                    </Button>                          
                                    <em className="align-self-center text-dark text-bold">  Exporter au format CSV </em>
                                </ExportCSVButton>
                                {/*print icon */}
                                <Link to="" title="Imprimer la liste" className="d-flex mr-3">
                                    <Button color="secondary">
                                        <em className="fa fa-print text-purple "></em>
                                    </Button>                                
                                    <em className="align-self-center text-dark text-bold">  Imprimer la liste </em>
                                </Link>
                                <div className="col-xl-3">
                                    .
                                </div>
                                {/*updated date */}
                                <div className="col-xl-1 ml-auto p-0">
                                    <small className="mb-0 mt-1 text-dark">Mis à jour le : 04/05/21 14:24:18</small>
                                </div>
                            </div>
                        </div>                        
                    </div>
                  )
                }
              </ToolkitProvider>
                    {/*pagination list */}
                        <div className="ml-auto col-xl-3">
                            <PaginationListStandalone { ...paginationProps } />
                        </div>
                    </div>
        );


        return(
            isLoading? (
                <PageLoader/>
            ):(
                <ContentWrapper>
                    <HeadingMenu path = {this.state.path}/>
                    <PaginationProvider pagination={paginationFactory(options)}>
                        { contentTable }
                    </PaginationProvider>
                </ContentWrapper>
            )

        )
    }
}

export default Activites;